import { Component, Input, OnInit } from '@angular/core';
import { ActivatedRoute, ParamMap } from '@angular/router';
import { Location }                 from '@angular/common';

//import switch map
import 'rxjs/add/operator/switchMap';

import { HeroService } from './hero.service';
import { Hero } from './hero';

@Component({
  selector: 'hero-detail',
  templateUrl: 'hero-detail.component.html',
  styleUrls: ['hero-detail.component.css']
})


//the exported class should be in upper camel case and end with Component
export class HeroDetailComponent implements OnInit {
    
    //input the hero
    // receive a hero object through its hero input property
        // and then bind to that property with its template.
    @Input() hero: Hero;

    constructor(
        private heroService: HeroService,
        private route: ActivatedRoute,
        private location: Location
    ) {}

    ngOnInit(): void {
        //maps the id to a new observeable
        this.route.paramMap
        //observable to extracts the id parameter value from the ActivatedRoute
        //NOTE** still need a getHero() in the service, in this case handled by a promise
        .switchMap((params: ParamMap) => this.heroService.getHero(+params.get('id')))
        .subscribe(hero => this.hero = hero);
    }

    //takes the user back
    goBack(): void {
        this.location.back();
    }
}


